import { Stack } from "./Stack";

class StackArr {
  constructor () {
    this._addStageResource = [] //
    this._stageResource = []//
    this._stageData = [] //
    this._addStage = [] //
    this._stackArr =[]//
    this._targetStack ={}
    this._maxLength = 100
  }

  add (id, objArr) {//
      if (id) {
        if (id !== this._targetStack._stackId) {//
          if (this.seek(id)._stackId || this.seek(id)._stackId === 0) this._targetStack = this.seek(id);//
          else {
            this._targetStack = new Stack(id);//
            this._stackArr.push(this._targetStack);
          }
        }
        this._targetStack.add(objArr);
      } else { console.warn('ID') }
  }
 

  undo (id) {
    if (id == "resource") {//
      if (this._addStageResource.length > 0) {
        this._stageResource.unshift(this._addStageResource.pop())
        return this._stageResource[0]
      } else {
        return undefined
      }
    } else if (!id && id != 0) {//
      if (this._stageData.length > 0) {
        this._addStage.unshift(this._stageData.pop())
        return this._addStage[0]
      } else {
        return undefined
      }
    } else {//
      if (id) {
        if (id !== this._targetStack._stackId) this._targetStack = this.seek(id);
        if (this._targetStack._stackId || this._targetStack._stackId === 0) return this._targetStack.undo()
        else return undefined
      } else { 
        return undefined
      }
    }
  }
  
  redo (id) { 
    if(id == 'resource') {//
      if (this._stageResource.length > 0) {
        this._addStageResource.push(this._stageResource.shift())
        return this._addStageResource[this._addStageResource.length - 1]
       } else {
         return undefined
       }
    } else if (!id && id != 0) {//
     if (this._addStage.length > 0) {
       this._stageData.push(this._addStage.shift())
       return this._stageData[this._stageData.length - 1]
      } else {
        return undefined
      }
   } else {//
     if (id) {
       if(id !== this._targetStack._stackId) this._targetStack = this.seek(id);
       if (this._targetStack._stackId || this._targetStack._stackId === 0) return this._targetStack.redo()
       else return undefined
      } else {
        return undefined
      }
    }
  }

  seek (id) {
    let target = {}
    if (id) {
      if (this._stackArr.length > 0) {
        this._stackArr.map((obj) => {
          if(obj.stackId === id) target = obj
        })
      }
    }
    return target
  }

  stageAdd (data) {//
    this._stageData.push(data)
    // console.log(this._stageData)
    this._addStage = []
  }

  stageResourceAdd (data) {//
    this._addStageResource.push(data)
    // console.log(this._addStageResource,"")
    this._stageResource = []
  }

  setLength (length, id) {
    //  lengthreturn
    if (id) {//
      let target = this.seek(id)
      if (target._stackId) target._maxLength = length
    } else {//
      this._stackArr.map((obj) => {
        obj._maxLength = length
      })
    }
  }

  isRepeated (obj_new, obj_old){//truefalse
    if (obj_new == obj_old) return true
    let count1 = 0, count2 = 0
    for (let k in obj_old) {
      count1++
      if (typeof(obj_old[k]) == 'object' && obj_new[k] && typeof(obj_new[k]) == 'object' && obj_old[k] != null && obj_new[k] != null) {
        if(!this.isRepeated(obj_old[k], obj_new[k])) return false
      } else if (obj_old[k] != obj_new[k])  return false
    }
    for (let k in obj_new) count2++
    if (count2 == count1) return true
    else return false
  }

  setStoreType () {
    console.log("")
    //  (1:   2:)
  }
}
export { StackArr }



