import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as mutations from './mutations'
import * as getters from './getters'
const state = {
  "typeObj":{},//
  "pixiObj":{},//pixi 
  "rootStageObj":{},//
  "stageObj":{},//
  "stageArr":[],//
  "stageMove":false,//
  "moveTargetObj":false,
  "stack":{},
  "stagePageFoucs":false,//stagePage
  "stageParentDom":{width:940,height:530,scale:1},//
  "stageParentSize":{width:940,height:530,widthScale:1,heightScale:1,scale:1},//stage
  "stageNameIndex":1,
  "selectorObj":{},
  "proportionLock":false,
  "copyObjArr":[],
  "limitAlert":true,//
  "typeOfQuestion":'',
  "frameAlert":false,
  "isUndoAfter":false,
  "lidContent":false,
  "animationBool":true,
  "onceStart":true,
};
Vue.use(Vuex);
export default new Vuex.Store({
  state,
  actions,
  mutations,
  getters
})



