class Active{
  constructor(activeName,activeType,listId){
    this._activeName = activeName;
    this._activeType = activeType;
    this._listId = listId;
    this._list = [];
  };

  get activeType() {
    return this._activeType;
  }

  get list() {
    return this._list;
  }
  set list(value){
    this._list.push(value)
  }
  toJson(){
    return JSON.parse(this._list);
  }
}
export{Active}

const type = {
  "DELETE_STAGE":"1",//
  "DELETE":"2",//
  "ADD_STAGE":"3",//
  "ADD":"4",//
  "LOCK":"5",//
  "HIDE":"6",//
  "GROUP":"7",//
  "CANCEL_GROUP":"8",//
  "CHANGE_LAYER":"9",//
  "CHANGE_STAGE_LAYER":"10",//
  "CHANGE_PROPERTY":"11"//
};



